import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import * as actions from '../../store/action/index'
import { updateObject, checkValidity } from '../../shared/utility'
import Input from '../../components/UI/Input/Input'
import Button from '../../components/UI/Button/Button'
import Spinner from '../../components/UI/Spinner/Spinner'
import classes from './Auth.css'

class Auth extends Component {
  state = {
    controls: {
      email: {
        //key
        elementType: 'input', // harus sama dengan type text html
        elementConfig: {
          type: 'email',
          placeholder: 'your email',
        },
        value: '',
        validation: {
          required: true,
          isEmail: true,
        },
        valid: false,
        touched: false,
      },
      password: {
        //key
        elementType: 'input', // harus sama dengan type text html
        elementConfig: {
          type: 'password',
          placeholder: 'your password',
        },
        value: '',
        validation: {
          required: true,
          minLength: 6,
        },
        valid: false,
        touched: false,
      },
    },
    isSingup: true,
  }

  componentDidMount() {
    if (!this.props.buildingBurger && this.props.authRedirectPath !== '/') {
      this.props.onSetRedirectPath()
    }
  }

  inputChangeHandler = (event, controlName) => {
    const updatedControls = updateObject(this.state.controls, {
      [controlName]: updateObject(this.state.controls[controlName], {
        value: event.target.value,
        valid: checkValidity(
          event.target.value,
          this.state.controls[controlName].validation
        ),
        touched: true,
      }),
    })
    this.setState({
      controls: updatedControls,
    })
  }

  submitHandler = (event) => {
    event.preventDefault()
    this.props.onAuth(
      this.state.controls.email.value,
      this.state.controls.password.value,
      this.state.isSingup
    )
  }

  switchAuthModeHandler = () => {
    this.setState((prevState) => {
      return { isSingup: !prevState.isSingup }
    })
  }

  render() {
    const formElementArray = []
    for (let key in this.state.controls) {
      formElementArray.push({
        id: key,
        config: this.state.controls[key],
      })
    }

    let form = formElementArray.map((form) => {
      return (
        <Input
          key={form.id}
          elementType={form.config.elementType}
          elementConfig={form.config.elementConfig}
          value={form.config.value}
          invalid={!form.config.valid}
          shouldValidate={form.config.validation}
          touched={form.config.touched}
          changed={(event) => this.inputChangeHandler(event, form.id)}
        />
      )
    })

    if (this.props.loading) {
      form = <Spinner />
    }

    let errorMessage = null
    if (this.props.error) {
      errorMessage = <p>{this.props.error.message}</p>
    }

    let authRedirect = null
    if (this.props.isAuthenticated) {
      authRedirect = <Redirect to={this.props.authRedirectPath} />
    }
    return (
      <div className={classes.Auth}>
        {authRedirect}
        {errorMessage}
        <form onSubmit={this.submitHandler}>
          {form}
          <Button btnType="Success">Submit</Button>
        </form>
        <Button clicked={this.switchAuthModeHandler} btnType="Danger">
          SWITCH TO {this.state.isSingup ? 'SIGIN' : 'SIGNUP'}
        </Button>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    loading: state.auth.loading,
    error: state.auth.error,
    isAuthenticated: state.auth.token !== null,
    buildingBurger: state.burgerBuilder.building,
    authRedirectPath: state.auth.authRedirectPath,
  }
}

const mapDispatchToPorps = (dispatch) => {
  return {
    onAuth: (email, password, isSingup) =>
      dispatch(actions.auth(email, password, isSingup)),
    onSetRedirectPath: () => dispatch(actions.setAuthRedirectPath('/')),
  }
}

export default connect(mapStateToProps, mapDispatchToPorps)(Auth)



